   background-color: transparent;\n    border: 0;\n\n    &:focus {\n        outline: 0;\n    }\n}\n\n/*========================================================================== */\n\n.c-btn {\n    // @include typo_aeonik_14-16;\n    position: relative;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    height: 56px;\n    padding: 20px 32px;\n    font-weight: 400;\n    letter-spacing: 0.03em;\n    border-style: solid;\n    border-width: 1px;\n    border-radius: 30px;\n    transition: background-color 0.25s, border-color 0.25s;\n\n    [class^='icon-'] {\n        display: inline-block;\n        font-size: 24px;\n    }\n\n    &:not(.-rounded) {\n        [class^='icon-'] {\n            margin-left: 8px;\n        }\n    }\n    &.-primary,\n    &.-primary2,\n    &.-primary3 {\n        // color: $color_white;\n        // background-color: $color_pumpkin;\n        // border-color: $color_pumpkin;\n\n        &:hover {\n            // background-color: $color_pumpkin_dark;\n            // border-color: $color_pumpkin_dark;\n        }\n\n        &.-alt {\n            // background-color: $color_cinder;\n        }\n    }\n    &.-primary2,\n    &.-primary3 {\n        // background-color: $color_cinder;\n        // border-color: $color_cinder;\n    }\n\n    &.-secondary {\n        font-weight: 400;\n        // color: $color_cinder;\n        // background-color: $color_white;\n        // border-color: $color_cinder;\n        transition: text-shadow 0.25s;\n\n        &:hover {\n            // text-shadow: 0 0 1px $color_black;\n        }\n    }\n    &.-borderless {\n        position: relative;\n        padding: 0;\n        font-weight: 400;\n        // color: $color_cinder;\n        background-color: transparent;\n        border: 0;\n        transition: 0.5s;\n\n        &:hover {\n            // text-shadow: 0 0 1px $color_black;\n            transition: 0.25s;\n        }\n\n        &.-bounce {\n            \n        }\n        @keyframes bounce {\n   