zawsze w skali 8px od labelu tekstowego.\n            */\n        font-weight: 400;\n        // border-color: $color_grey-2;\n\n        &:hover {\n            // border-color: $color_cinder;\n        }\n    }\n    &.-add {\n        font-weight: 400;\n        // border: 1px solid $color_grey-4;\n\n        [class^='icon-'] {\n            margin-right: 10px;\n            margin-left: 0;\n        }\n\n        &:hover {\n            // color: $color_white;\n            // text-shadow: 0 0 1px $color_white;\n            // background-color: $color_cinder;\n        }\n    }\n    &.-rounded {\n        width: 56px;\n        height: 56px;\n        padding: 0;\n        // background-color: $color_pumpkin;\n        // border-color: $color_pumpkin;\n        &:hover {\n            // background-color: $color_pumpkin-dark;\n            // border-color: $color_pumpkin-dark;\n        }\n        &.-disabled {\n            // background-color: rgba($color_pumpkin, 0.2);\n            border-color: transparent;\n        }\n    }\n    &.-cart {\n        // color: $color_white;\n\n        .c-quantity {\n            // @include typo_aeonik_11-12;\n            position: absolute;\n            top: 0;\n            right: 0;\n            z-index: 1;\n            display: flex;\n            align-items: center;\n            justify-content: center;\n            min-width: 18px;\n            min-height: 18px;\n            font-weight: 700;\n            // color: $color_white;\n            // background-color: $color_cinder;\n            // border: 2px solid $color_white;\n            border-radius: 50px;\n            &.-empty {\n                display: none;\n            }\n        }\n        &.-disabled {\n            pointer-events: all;\n        }\n    }\n}\n\n/******************************************************/\n\n.c-link {\n    // @include typo_aeonik_14-20;\n    position: relative;\n    display: inline-block;\n    padding-bottom: 4px;\n    letter-spacing: 0.02em;\n    cursor: pointer